.container {
  width: min(100% - 30px, 1200px);
  margin-inline: auto;
}

.flex {
  display: flex;
  justify-content: space-between;
}

h1, h2, p, span, li, a, button {
  font-family: 'Poppins', sans-serif;
}

header {
  height: 60px;
  background-color: rgb(0 0 0 / 85%);
}

h1 {
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 60px;
  letter-spacing: 0.2px;
}

nav ul {
  display: flex;
  gap: 20px;
}

nav li {
  line-height: 60px;
  list-style: none;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}

nav li:hover {
  color: #6f4e37;
}

.banner {
  height: 600px;
  background: url(../img/coffee-bg.jpg) center 10% / cover no-repeat;
  display: grid;
  place-items: center;
}

.banner::before {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  height: 600px;
  background-color: rgb(0 0 0 / 0.6);
  content: '';
}

.banner-call {
  z-index: 99;
  text-align: center;
  color: #fff;
}

.banner-call h2 {
  font-size: 45px;
  font-weight: 700;
  padding-bottom: 15px;
}

.banner-call p {
  font-size: 18px;
  color: rgb(255 255 255 / 0.85);
}

.banner-buttons {
  margin-top: 50px;
}

button {
  width: 130px;
  height: 44px;
  font-size: 1.6rem;
  font-weight: 600;
  border-radius: 10px;
  cursor: pointer;
}

.secondary {
  margin-right: 30px;
  border: 2px solid #4b3624;
  color: #4b3624;
}

.primary {
  background-color: #4b3624;
  color: #fff;
}

.primary:hover {
  background-color: #5c432f;
}

.secondary:hover {
  background-color: rgb(255 255 255 / 85%);
}

.products {
  background-color: rgb(131 131 131 / 0.15);
  padding: 60px 0;
}

.products-display {
  display: flex;
  gap: 12px;
}

img {
  height: 400px;
  width: 300px;
  border-radius: 5px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 0.05);
}

footer {
  display: flex;
  align-items: center;
  height: 80px;
  background-color: #111;
}

.social {
  display: flex;
  line-height: 80px;
  font-size: 22px;
  color: #fff;
  cursor: pointer;
}

.social li {
  padding-right: 10px;
}

.social li:hover {
  color: #6f4e37;
}

.copyright {
  color: #fff;
  font-size: 14px;
  font-weight: 600;
}